.container{
    background-color: "#F3F4F6" !important;
    padding-top: 28px;
}

header{
    justify-content: space-between;
    width: 86%;
    margin-left: auto;
    margin-right: auto;
}
header.home.menu{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}
header img{
    width: 233px;
}
header button{
    margin-left: 20px;
    margin-right: 20px;
    border: none;
    padding: 12px;
    padding-left: 18px;
    padding-right: 18px;
    border-radius: 4px;    
    font-family: 'Helvetica';
    font-size: 16px;
}
header .connexionButton{
    background-color: #FFFFFF;
    color: #16023F;
}
header .compteButton{
    background-color: #16023F;
    color: #FFFFFF;
}
section.hero{
    display: flex;
    flex-direction: row;
    width: 86%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 80px;
}
section.hero div.description{
    display: flex;
    flex-direction: column;
    width: 70%;
    color: #16023F;
    padding-top: 40px;
}
section.hero div.description h1{
    font-family: 'Helvetica';
    font-size: 44px;
}
section.hero div.description p{
    font-family: 'Helvetica';
    font-size: 26px; 
}
section.hero div.description button{
    width: 200px;
    font-family: "Helvetica";
    font-size: 14px;
    display: flex;
    align-items: center;
    color: #16023F;
    border-color: #16023F;
    border-radius: 8px;
    border-width: 1px;
    height: 28px;
    margin-bottom: 12px;
}
section.hero div.description button.play-store{
    background-color: #16023F;
    color: white;
}
section.hero div.description button svg{
    margin-right: 8px;
}
section.hero div.image img{
    width: 347px;
}
section.description-1{
    margin-top: 30px;
    position: relative;
    overflow-x: hidden;
}
section.description-2{
    margin-top: -40px;
    position: relative;
}
section.description-1 div.card-1, section.description-2 div.card-1-2{
    width: 86%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 90px;
    background-image: linear-gradient(240deg, #69DAC7, #1A99B6);
    border-radius: 8px;
    padding-left: 20px;
    padding-top: 30px;
    padding-bottom: 20px;
}
section.description-2 div.card-1-2{
    background-image: linear-gradient(120deg, #F4AF6E, #F43FB1);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
section.description-1 div.card-1 p, section.description-1 div.card-1 h2{
    width: 50%;
    font-family: 'Helvetica';
    color: #FFFFFF;
}
section.description-2 div.card-1-2 p, section.description-2 div.card-1-2 h2{
    width: 40%;
    font-family: 'Helvetica';
    color: #FFFFFF;
    margin-right: 36px;
}
section.description-1 div.card-1 p.last-text{
    width: 96%;
    text-align: right;
    padding-right: 32px;
    margin-top: 80px;
    margin-bottom: 0px;
    font-size: 60px;
}
section.description-2 div.card-1-2 div.last-text-block{
    width: 50%;
    display: flex;
    flex-direction: column;
    align-self: flex-start;
}
section.description-1 div.card-1 p, section.description-2 div.card-1-2 p{
    font-size: 20px;
    margin-top: 0px;
}
section.description-2 div.card-1-2 p{
    left: 50%;
}
section.description-1 div.card-1 h2, section.description-2 div.card-1-2 h2{
    font-size: 36px;
}
section.description-1 div.card-1 button{
    border-color: #FFFFFF;
    border-width: 1px;
    border-radius: 4px;
    color: #FFFFFF;
    background-color: transparent;
    padding-left: 14px;
    padding-right: 14px;
    padding-top: 8px;
    padding-bottom: 8px;
    font-family: 'Helvetica';
}
section.description-1 div.card-2{
    display: flex;
    align-self: flex-end;
    width: 50%;
    position: absolute;
    right: -30px;
    top: 0px;
    background-image: linear-gradient(240deg, #69DAC7, #1A99B6);
    border-radius: 16px;
}
section.description-2 div.card-2-2{
    width: 50%;
    position: absolute;
    top: -90px;
    left: -30px;
    border-radius: 16px;
}
section.description-1 div.card-2 img, section.description-2 div.card-2-2 img{
    width: 100%;
}
section.description-3{
    width: 87%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: row;
    margin-top: 80px;
}
section.description-3 div.description-3-1{
    width: 40%;
    margin-right: 12px;
    border-radius: 12px;
    background-color: #16023F;
    padding: 0px 42px 18px 24px;
}
section.description-3 div.description-3-1 h2,
section.description-3 div.description-3-2 h2{
    color: white;
    font-size: 36px;
    font-family: "Helvetica";
}
section.description-3 div.description-3-2 h3{
    color: white;
    font-size: 16px;
    font-family: "Helvetica";
}
section.description-3 div.description-3-1 p{
    color: white;
    font-size: 14px;
    font-family: 'Helvetica';
}
section.description-3 div.description-3-2{
    background-color: #702EF4;
    width: 46%;
    border-radius: 12px;
    margin-left: 12px;
    padding: 0px 42px 18px 24px;
}
footer{
    width: 87%;
    margin-top: 86px;
    margin-left: auto;
    margin-right: auto;
}
footer img{
    width: 140px;
    display: block;
    margin-left: auto; 
    margin-right: auto;
}
footer ul{
    list-style: none;
    float: left;
    padding: 0px;
}
footer ul li{
    display: inline;
}
footer nav ul{
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 30px;
}
footer nav ul li{
    color: #16023F;
    font-family: 'Helvetica';
    font-size: 14px;
}
footer ul.copyright{
    height: 38px;
    border-radius: 16px;
    background-color: #16023F;
    display: flex;
    align-items: center;
}
footer ul.copyright li{
    color: white;
    font-family: "Helvetica";
    font-size: 14px;
    padding-left: 25px;
    padding-right: 25px;
    height: 100%;
    display: flex;
    align-items: center;
}
footer ul.copyright li:nth-child(2){
    border-left-style: solid;
    border-left-width: 1px;
    border-left-color: white;
    border-right-style: solid;
    border-right-width: 1px;
    border-right-color: white;
}
footer ul.copyright a{
    color: white;
}
.section-inscription-button{
    background-color: transparent;
    border-color: white;
    border-style: solid;
    border-width: 1px;
    color: white;
    width: 280px;
    height: 40px;
    border-radius: 8px !important;
}
section.description-2 div.card-1-2 p.last-text-2{
    color: white;
    font-family: "Helvetica";
    font-size: 60px;
    width: 100%;
    margin-bottom: 24px;
}
.mobile-view{
    display: none;
}
@media screen and (max-width:1024px){
    section.description-1 div.card-1 p.last-text{
      margin-top: 0px !important;
    }
    section.description-2 div.card-1-2 div.last-text-block{
      margin-top: -150px;
    }
    section.description-2 div.card-1-2 h2{
      font-size: 34px !important;
    }
}
@media screen and (max-width:1023px){    
    header, main, footer, header>*{
        display: none;
    }
    body{
        background-color: #F3F4F6;
    }
    .mobile-view{
        display: flex;
        flex-direction: column;
        max-width: 320px;
        margin-left: auto;
        margin-right: auto;
    }
    .mobile-view img{
        width: 120px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 40px;
        margin-top: 120px;
    }
    .mobile-view h1{
        color: #16023F;
        font-family: "Helvetica";
        font-size: 24px;
        text-align: center;
        margin-bottom: 0px;
    }
    .mobile-view p{
        color: #16023F;
        font-family: "Helvetica";
        font-size: 12px;
        text-align: center;
        margin-top: 0px;
    }
    .mobile-view button{
        height: 40px;
        width: 240px;
        border-radius: 12px;
        border-style: solid;
        border-width: 1px;
        margin-left: auto;
        margin-right: auto;
        font-size: 14px;
        font-weight: bold;
        margin-top: 8px;
        margin-bottom: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .mobile-view button.mobile-app-store{
        border-color: #F53BB6;
        color: #16023F; 
        margin-top: 100px;       
    }
    .mobile-view button.mobile-app-store img{
        height: 30px;
        width: auto;
        margin: 0px;
        margin-right: 8px;
    }
    .mobile-view button.mobile-app-store p{
        margin: 0px;        
        font-size: 14px;
        font-weight: bold;
    }
    .mobile-view button.mobile-play-store{
        background-image: linear-gradient(262deg, #F53BB6, #F4AF6E);
        border-color: transparent;
        color: white;
    }
    .mobile-view button.mobile-play-store svg{
        transform: scale(1.5);
        margin-right: 8px;
    }
}